'use client'
import { useState } from 'react'
import Image from 'next/image'
import Icon from '../public/icon.png'
import './globals.css'

export default function Page() {
  const [form, setForm] = useState({ name: '', email: '', message: '' })
  const [resp, setResp] = useState('')
  const handleChange = e => {
    setForm({ ...form, [e.target.name]: e.target.value })
  }
  const handleSubmit = async e => {
    e.preventDefault()
    const res = await fetch('/api/contact', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(form)
    })
    if (res.ok) {
      setResp(`Gracias, ${form.name}! Te contactaremos pronto.`)
      setForm({ name: '', email: '', message: '' })
    } else {
      setResp('Error al enviar. Intenta de nuevo.')
    }
  }
  return (
    <>
      <header>
        <Image src={Icon} alt="Logo" width={80} height={80} />
        <h1>PokeDev</h1>
        <p>Startup de desarrollo de software</p>
        <nav>
          <ul>
            <li><a href="#about">Sobre Nosotros</a></li>
            <li><a href="#services">Servicios</a></li>
            <li><a href="#contact">Contacto</a></li>
          </ul>
        </nav>
      </header>
      <main>
        <section id="about">
          <h2>Sobre Nosotros</h2>
          <p>En PokeDev creamos soluciones de software a medida para impulsar tu negocio.</p>
        </section>
        <section id="services">
          <h2>Servicios</h2>
          <ul className="services-list">
            <li>Desarrollo Web</li>
            <li>Aplicaciones Móviles</li>
            <li>Consultoría Técnica</li>
          </ul>
        </section>
        <section id="contact">
          <h2>Contacto</h2>
          <form onSubmit={handleSubmit}>
            <input name="name" value={form.name} onChange={handleChange} placeholder="Nombre" required />
            <input type="email" name="email" value={form.email} onChange={handleChange} placeholder="Correo" required />
            <textarea name="message" value={form.message} onChange={handleChange} placeholder="Mensaje" required />
            <button type="submit">Enviar</button>
          </form>
          <div id="response">{resp}</div>
        </section>
      </main>
      <footer>© 2025 PokeDev</footer>
    </>
  )
}
